class utilsModule {

    /**  */
    public isMobile() {
        const pattern = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|OperaMini/i;
        return pattern.test(navigator.userAgent);
    }
    
    /**
     * rem 
     * @param el 
     */
    public remSetting(el: HTMLElement | null) {
        if (!el) return;
        const html = document.documentElement; //   document.body
        /**  */
        let value = 375 / 50;
        /**  */
        let width = el.getBoundingClientRect().width;
        html.style.fontSize = width / value + 'px';
        // 
        window.addEventListener('resize', function() {
            width = el.getBoundingClientRect().width;
            html.style.fontSize = width / value + 'px';
        });
    }
    
    /**
     * 
     * @param value  
     * @param success 
     * @param fail 
     */
    public copyText(value: string, success?: Function, fail?: Function) {
        value = value.replace(/(^\s*)|(\s*$)/g, '');
        if (!value) {
            if (fail) fail('');
            return;
        }
        const clipboard = document.createElement('textarea');
        document.body.appendChild(clipboard);
        clipboard.value = value;
        clipboard.style.cssText = 'font-size: 15px; padding: 4px 8px; position: fixed; top: -1000%; left: -1000%;';
        clipboard.setAttribute('readonly', '');
        clipboard.setAttribute('cols', '50');
        clipboard.setAttribute('rows', '50');
        clipboard.select();
        clipboard.setSelectionRange(0, clipboard.value.length);
        document.execCommand('copy');
        if (clipboard.parentNode) clipboard.parentNode.removeChild(clipboard);
        if (success) success();
    }

    /**
     * 
     * @param array 
     * @param count 
     */
    public getRandomArrayElements(array: Array<any>, count: number) {
        /**  */
        let length = array.length;
        /**  */
        let min = length - count, temp, range;
        while (length-- > min) {
            range = Math.floor((length + 1) * Math.random());
            temp = array[range];
            array[range] = array[length];
            array[length] = temp;
        }
        return array.slice(min);
    }

    /**
    * 
    * @param array
    */
    public shuffleArray(array: Array<any>) {
        let random = () => Math.random() > 0.5 ? -1 : 1;
        return array.sort(random);
    }

    /**
     * 
     * @param min 
     * @param max 
     */
    public ranInt(min: number, max: number) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    /**
     * js
     * @param link src
     * @param callback 
     */
    public insertScript(link: string, callback?: Function) {
        var label = document.createElement('script');
        label.src = link;
        label.onload = function () {
            if (callback) callback();
            if (label.parentNode) label.parentNode.removeChild(label);
        }
        document.body.appendChild(label);
    }
}

/**  */
const uiils = new utilsModule();

export default uiils;